@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,400;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,400;0,600;1,400;1,600&display=swap);
:root {
  --banner-color: var(--logo-orange);
  --banner-color-accent: white;
  /* decorative text color*/
  --text-color-deco: var(--logo-navy);
  --sidebar-orange: var(--logo-orange);
  --logo-navy: #203a83;
  --logo-green: #7bbf50;
  --logo-orange: #e89620;
  --logo-tan: #f0c67d;
  --logo-blue: #1fbcf2;
  --logo-dark-gray: #5d5858;
  --logo-darkest-gray: #39383a;
  --near-black: #1f1f1f;
  --logo-pink: #f08e8e;
}


body {
  margin: 0;
  font-family: Montserrat, arial, sans-serif;
}

.margins {
  margin-left: 40px;
  margin-right: 40px;
}

:root {
  /* yearly banner variables */
  --year-nav-pointed-arrow: var(--banner-color);
  --year-nav-drop-shadow: rgb(38, 122, 174);

  /* link interaction colors */
  --link-hover: rgb(127, 127, 206);
  --link-active: rgb(35, 159, 74);

  /* footer social link variables */
  --facebook: rgb(59, 89, 152);
  --twitter: rgb(29, 161, 242);
  --google: rgb(15, 157, 88);
  --youtube: rgb(255, 0, 0);

  /* map section regions */
  --north: #f9f22b;
  --capital: #1deab9;
  --mid-hudson: #2d8eff;
  --nyc: #ffca29;
  --long-island: #c152f3;
  --mohawk-valley: #ff544e;
  --southern: #5cee29;
  --finger-lakes: #fa446c;
  --western: #2dddff;
  --central: #9488ff;
  /* --#fa446c */
}

.logoHero__container {
  background-image: linear-gradient(110deg, #213d88, #213d88 25%, #1dbbf1);
  /* background-color: var(--logo-orange); */
  max-width: 100vw;
  background-repeat: no-repeat;
  background-size: contain;
  text-align: center;
  align-items: middle;
}

.logoHero__container img {
  max-width: 100vw;
  max-height: 320px;
}

.paragraph {
  margin-top: 40px;
  margin-bottom: 40px;
  font-weight: bold;
  border-bottom: 50px solid var(--logo-navy);
}

.what__picture {
  max-width: 300px;
  height: 300px;
  margin: 0 1rem;
  object-fit: cover;
}

.what__header-text {
  text-transform: uppercase;
  color: var(--logo-navy);
  position: relative;
  font-size: 2.5rem;
  font-weight: bold;
  font-family: "Barlow Semi Condensed", Montserrat, Arial, sans-serif;
}

.what__header-bg {
  height: 30px;
  width: 250px;
  max-width: 100vw;
  position: relative;
  z-index: -1;
  margin: -1rem;
  background: var(--logo-orange);
  top: -20px;
  left: 45px;
}

@media (max-width: 767px) {
  .what__picture {
    max-width: 200px;
  }

  .what__header-text {
    font-size: 2rem;
    margin-bottom: 1rem;
  }

  .what__header-bg {
    height: 22px;
    width: 175px;
    top: -22px;
  }
}

@media (min-width: 425px) {
  .what__picture {
    float: left;
  }
}

.why {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.why__picture {
  max-width: 300px;
  object-fit: contain;
}

.why__header-text {
  text-transform: uppercase;
  color: var(--logo-navy);
  position: relative;
  font-size: 2.5rem;
  font-weight: bold;
  font-family: 'Barlow Semi Condensed', Montserrat, Arial, sans-serif;
}

.why__header-bg {
  height: 30px;
  width: 350px;
  max-width: 100vw;
  position: relative;
  z-index: -1;
  margin: -1rem;
  background: var(--logo-orange);
  top: -20px;
  left: 45px;
}

@media (max-width: 767px) {
  .why__picture {
    max-width: 200px;
  }

  .why__header-text {
    font-size: 2rem;
    margin-bottom: 1rem;
  }

  .why__header-bg {
    height: 22px;
    width: 175px;
    top: -22px;
  }
}

@media (min-width: 425px) {
  .why__picture {
    float: right;
  }
}

.impact__header {
  text-transform: uppercase;
  color: var(--logo-navy);
  position: relative;
  font-size: 2.5rem;
  font-weight: bold;
  font-family: "Barlow Semi Condensed", Montserrat, Arial, sans-serif;
}

.impact__header-bg {
  height: 30px;
  max-width: 550px;
  position: relative;
  z-index: -1;
  margin: -1rem;
  background: var(--logo-orange);
  top: -20px;
  left: 45px;
}

.impact__img {
  max-height: 200px;
  max-width: 100%;
  margin: 1rem;
}

.impact__inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}


.impact__part {
  padding: 1rem;
  font-weight: bold;
  max-width: 300px;
  max-height: 300px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.impact__slash {
  margin: auto 2rem;
  max-height: 200px;
  align-self: center;
}

@media (max-width: 767px) {
  .impact__header {
    font-size: 2rem;
    margin-bottom: 1rem;
  }

  .impact__header-bg {
    height: 22px;
    width: calc(100% - 22px);
    top: -22px;
  }

  .impact__img {
    max-height: 125px;
  }

  .impact__slash {
    max-height: 150px;
  }
}

@media (max-width: 576px) {
  .impact__header-bg {
    width: calc(100% - 22px);
    max-width: 100%;
  }

  .impact__slash {
    display: none;
  }

  .impact__inner {
    flex-direction: column;
  }

  .impact__img {
    max-width: 100%;
  }
}

.regionBanner__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--banner-color);
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 1rem;
}

.regionBanner__regionName {
  text-align: center;
  font-size: clamp(1rem, -0.875rem + 8.333vw, 4.5rem);
  font-weight: bold;
  text-transform: uppercase;
  color: var(--text-color-deco);
}

.regionBanner__rect {
  height: 55px;
  /* width: 850px; */
  background-color: var(--logo-orange);
  position: relative;
  top: 4rem;
  left: 1%;
  margin: -1.5rem;
  z-index: -1;
}

.errorMessage {
  display: flex;
  justify-content: center;
  font-size: 6rem;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.error {
  display: flex;
  /* justify-content: center; */
  flex-flow: column;
  height: 20%;
}

.error404 {
  font-size: 4rem;
  font-family: monospace;
  display: block;
  text-align: center;
  margin-bottom: 0;
}

.errorMessage {
  /* display: block; */
  font-size: 2rem;
  text-align: center;
  margin-top: 1rem;
}

.region__copyContainer {
  width: 100%;

  height: 20rem;
  display: flex;
  margin-top: 1rem;
  margin-bottom: 1rem;
  background-color: var(--logo-orange);
  padding: 1rem;
  border-radius: 10px;
  align-items: space;
  justify-content: space-between;
}

.keepRight {
  flex-flow: row-reverse;
  margin-right: 1rem;
}

.region__image {
  height: 100%;
  object-fit: contain;

  overflow: hidden;
}
video.region__image {
  height: 100%;
  object-fit: scale-down;
}
.region__imageContainer {
  display: flex;
  height: 100%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;

  max-width: 40%;
  object-fit: cover;
}

.region__copy {
  font-size: clamp(0.01rem, 5vw, 1rem);
  flex-flow: column wrap;

  height: 100%;
  position: relative;
  margin-left: 1rem;
  color: white;
  justify-content: space-between;
  scrollbar-color: #8b5509a4 var(--sidebar-orange);
  text-align: left;
  overflow-y: scroll;
  padding-left: 0.4rem;
  padding-right: 0.4rem;
}
div ::-webkit-scrollbar {
  background-color: #8b5509a4 var(--sidebar-orange);
}

div ::-webkit-scrollbar-thumb {
  background-color: #8b5509a4;
}
.addMarginRight {
  margin-right: 1rem;
  margin-left: 0rem;
}

/* image container */
.keepRight {
  justify-content: flex-end;
}
@media screen and (max-width: 800px) {
  .region__imageContainer {
    max-width: 40%;
    height: 40%;
    position: relative;
    float: left;
    object-fit: cover;
    margin-left: 0.5rem;
  }

  .region__copy {
    display: flex;
    flex-flow: column;
    margin-left: 0;
  }
  .region__copyContainer {
    display: flex;
    flex-flow: column;
    height: 24rem;
  }
  h6 {
    /* position: absolute; */
    text-align: right;
    display: flex;
    /* border-style: solid; */
    width: 100%;
    margin-right: 0.2rem;
    /* justify-content: flex-end; */
  }
}

.footer {
  background-image: linear-gradient(to bottom right, #1DBBF1 20%, #213D88);
  color: white;
  padding: 1rem 2rem;
}

.footer__columns {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.footer__info {
  color: white;
  letter-spacing: 0.025rem;
  font-size: 1rem;
}

.footer__info p {
  vertical-align: middle;
  margin-bottom: 0;
  margin-right: 1.2rem;
  display: inline-block;
}

.footer__social {
  display: flex;
}

.footer__social .social-bg {
  border-radius: 50%;
  height: 38px;
  width: 38px;
  margin: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.25);
}

.footer__social .twitter {
  background-color: #1DA1F2;
}

.footer__social .twitter:hover {
  background-color: #28aafa;
}

.footer__social .linkedin {
  background-color: #0e76a8;
}

.footer__social .linkedin:hover {
  background-color: #1280b3;
}

.footer__social .facebook {
  background-color: #3b5998;
}

.footer__social .facebook:hover {
  background-color: #4364ab;
}

.footer__social .instagram {
  background-color: #833AB4;
}

.footer__social .instagram:hover {
  background-color: #9041c4;
}

@media (max-width: 576px) {
  .footer__columns {
    flex-direction: column;
  }
}

.footer__social a {
  color: inherit;
}
